.gallery {
  .gallery__wrapper {
    padding-top: 25px;
  }

  .gallery__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .gallery__list-item {
    border-top: 1px solid #e5e5e5;
    display: block;
    margin: 0;
    padding: 25px 20px;
    color: #000000;
    text-align: left;
  }

  .gallery__list-item-title {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    padding-top: 15px;
    text-align: left;
  }

  .gallery__list-item-author {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    padding-top: 10px;
    text-align: left;
    text-transform: none;
  }

  .gallery__list-item-figure {
    padding: 0;
    margin: 0;

    img {
      background-position: bottom center, 50% 20%;
      background-repeat: no-repeat;
      background-size: auto, cover;
      width: 100%;
      height: auto;
    }
  }

  .gallery__list-item-likes {
    padding-top: 11px;
    display: flex;
    flex-direction: row;
  }

  .gallery__list-item-likes-icon {
    padding-right: 14px;
  }

  .gallery__list-item-likes-amount {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
  }
}
